
    .slider {
        position: relative;
        width: 80%;
        height: 500px;
        margin: auto;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        overflow: hidden;
    }

    .slider-img {
        position: absolute;
        width: 100%;
        height: 500px;
        transition: all 1s ease-in-out;
    }

    .slider-img-prev {
        transform: translateX(-100%);
    }

    .slider-img-next {
        transform: translateX(100%);
    }

